import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { RoutineDict } from 'tsx-fitelo';
import moment from 'moment';
import { ngxFiteloConfig, NgxFiteloConfig } from '../../tokens';

@Injectable({
  providedIn: 'root',
})
export class UserRoutineResource {

  constructor(
    @Inject(ngxFiteloConfig) private config: NgxFiteloConfig,
    private http: HttpClient,
  ) {}

  async getRoutine(id: number, date) {
    let data;
    try {
      data = await this.http.get<RoutineDict>(`${this.config.API_URL}/users/${id}/routines/${date}`).toPromise();
    }
    catch (e) {
      data = null;
    }
    return data;
  }

  async updateRoutine(id: number, date: string, mealId: number, value: boolean) {
    let payload = value;
    date = moment(date).format('YYYY-MM-DD');
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'text/plain',
        Authorization: 'my-auth-token'
      })
    }
    const url = `${this.config.API_URL}/users/${id}/routines/${date}/mealtaken/${mealId}`;
    return this.http.put(url, payload, httpOptions).toPromise();
  }

  async getGroceries(id: number) {
    let fiterparam = new HttpParams().set('filters', JSON.stringify(this.getFilersArray()));
    const res = await this.http.get<Routines>(`${this.config.API_URL}/users/${id}/routines`,{params: fiterparam}).toPromise();

    let routine: any = res.data
    let newgroceriesDict: any = {};
    routine.forEach((val) => {
      val.mealDicts.forEach((meal) => {
        meal.recipeDicts.forEach((recipe) => {
          if(recipe.groceryDicts) {
            recipe.groceryDicts.forEach((grocery) => {
              newgroceriesDict[grocery.id] = grocery;
            })
          }
        })
      })
    })

    return Object.values(newgroceriesDict);
  }

  getFilersArray() {
    const today = moment().format("YYYYMMDD");

    let filersArray = [
      {
        "field": "date",
        "operator": ">=",
        "value": today,
      },
      {
        "field": "date",
        "operator": "<=",
        "value": moment().add(7,'day').format("YYYYMMDD")
      }
    ]

    return filersArray;
  }

}

interface Routines {
  data?: RoutineDict[],
  paging?: any
}
