import { Injectable } from "@angular/core";
import { AngularFirestore } from "@angular/fire/firestore";

@Injectable({
  providedIn: 'root',
})
export class PlansResource {

  constructor(
    private firestore: AngularFirestore,
  ) {}

  async mget(planIds: string[]) {
    const dbPlans: any[] =  (await this.firestore.collection('plans', q => q.where('id', 'in', planIds)).get().toPromise()).docs.map(d => d.data());
    const plans: any[] = [];
    planIds.forEach(planId => {
      plans.push(Object.assign({}, dbPlans.find(p => p.id == planId)));
    })
    // FIX: firestore merge same planIds
    return plans;
  }

}
